import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
import Swal from 'sweetalert2';
import { useUserStore } from '@/stores/user';
export default {
  components: {
    AgGridVue,
    Swal
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      rowData: [{
        fac_code: "",
        model_name: "",
        fac_location: "",
        employee_code: useUserStore().empName,
        employee_name: useUserStore().id,
        fac_status: ""
      }],
      searchType: "fac_code",
      searchText: "",
      columnDefs: [{
        field: "fac_code",
        header: "설비코드",
        flex: 1
      }, {
        field: "model_name",
        header: "설비이름",
        flex: 1
      }, {
        field: "fac_location",
        header: "설비위치",
        flex: 1
      }, {
        field: "employee_code",
        header: "담당자",
        flex: 1,
        valueFormatter: params => {
          return params.data?.employee_name || params.value;
        }
      }, {
        field: "fac_status",
        header: "설비상태",
        flex: 1,
        valueFormatter: params => {
          if (params.value == 'FS1') {
            return params.value = '가동';
          } else if (params.value == 'FS2') {
            return params.value = '비가동';
          }
        },
        cellStyle: params => {
          if (params.value == "FS1") {
            return {
              color: '#22C55E',
              textAlign: 'center',
              fontWeight: 'bold'
            };
          } else if (params.value == "FS2") {
            return {
              color: 'red',
              textAlign: 'center',
              fontWeight: 'bold'
            };
          }
          return null;
        }
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 5,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.facList();
  },
  methods: {
    close() {
      this.$emit("close");
    },
    facList() {
      axios.get('/api/fac/facList').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    searchMaterials() {
      axios.get('/api/fac/facList', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    clicked(event) {
      this.$emit('selectFac', event.data);
      this.close();
    },
    watch: {
      visible(newVal) {
        if (newVal) {
          this.facList();
        }
      }
    }
  }
};